import { useRouter } from "next/navigation";
import { useForm } from "react-hook-form"
import { yupResolver } from "@hookform/resolvers/yup"
import * as yup from "yup"


function RegisterForm()
{

  const schema = yup
  .object({
    name: yup.string().required("Preencha seu nome"),
    email: yup.string().email("O Email deve estar em um formato válido").required("Preencha o email"),
    password: yup.string().required("Preencha a senha"),
  })
  .required()


  const { control, register, handleSubmit, formState: { errors }, reset } = useForm({
    resolver: yupResolver(schema),
  });

  const newUser = async (data) => {
    const apiUrl = process.env.NEXT_PUBLIC_API_URL+"/users/register";
    console.log(apiUrl)
    try
    {
      const router = useRouter();
      const data = await fetch(apiUrl, {
        method: 'POST',
        body: data,
        headers: {
          'Content-Type': 'application/json',
        },
      })
  
      if(!data.ok){
        throw new Error(data.statusText || 'Algo deu errado')
      }
  
      //handletoken
  
      router.push('/login')
  
    }catch(error)
    {
      console.error('Erro:', error)
    }
  }

    return (
      <form 
        onSubmit={handleSubmit(newUser)}
        control={control}
        className="space-y-6">
            <div>
            <label htmlFor="name" className="block text-sm font-medium leading-6 text-gray-900">
              Nome
            </label>
            <div className="mt-2">
              <input
                defaultValue="" {...register("name")}
                className="pl-2 block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
              />
              <p>{errors.name?.message}</p>
            </div>
          </div>
          <div>
            <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
              Email
            </label>
            <div className="mt-2">
              <input
                defaultValue="" {...register("email")}
                className="pl-2 block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
              />
              <p>{errors.email?.message}</p>
            </div>
          </div>

          <div>
            <div className="flex items-center justify-between">
              <label htmlFor="password" className="block text-sm font-medium leading-6 text-gray-900">
                Senha
              </label>
            </div>
            <div className="mt-2">
              <input
                defaultValue="" {...register("password")}
                type='password'
                className="pl-2 block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
              />
              {errors.password?.message}
              
            </div>
          </div>

          <div>
            <input
                value="Registrar"
                type="submit"
                className="flex w-full justify-center rounded-md bg-indigo-600 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
            />
          </div>
        </form>   
    )
}

export default RegisterForm;